import React, { Component } from "react";
import SkillComponent from "./SkillComponent.js";
// import logo from './Components'

class Skills extends Component {
  constructor() {
    super();
    this.state = {
      imageURL: "https://image.flaticon.com/icons/png/512/56/56786.png",
      imageURL1: "https://img.icons8.com/ios/50/000000/star.png",
      skills: [
        { skill: "HTML", level: 5, id: 1 },
        { skill: "CSS", level: 4, id: 2 },
        { skill: "JavaScript", level: 4, id: 3 },
        { skill: "Google Firebase", level: 3, id: 4 },
        { skill: "ReactJS", level: 4, id: 5 },
        { skill: "Java", level: 3, id: 6 },
        { skill: "PHP", level: 3, id: 7 },
        { skill: "UI/UX", level: 4, id: 8 },
        { skill: "Adobe XD", level: 4, id: 9 },
        { skill: "Figma", level: 4, id: 10 },
        { skill: "Sketch", level: 3, id: 11 },
        { skill: "Adobe Photoshop", level: 3, id: 12 },
        { skill: "Adobe After Effects", level: 4, id: 13 },
        { skill: "Adobe Premiere Pro", level: 4, id: 14 },
      ],
    };
  }
  star() {
    return (
      <img
        src={this.state.imageURL}
        alt="filled star"
        className="filled-star"
      ></img>
    );
  }
  star1() {
    return (
      <img
        src={this.state.imageURL1}
        alt="unfilled star"
        className="unfilled-star"
      ></img>
    );
  }

  onestar() {
    return (
      <React.Fragment>
        {this.star()}
        {this.star1()}
        {this.star1()}
        {this.star1()}
        {this.star1()}
      </React.Fragment>
    );
  }
  twostar() {
    return (
      <React.Fragment>
        {this.star()}
        {this.star()}
        {this.star1()}
        {this.star1()}
        {this.star1()}
      </React.Fragment>
    );
  }
  threestar() {
    return (
      <React.Fragment>
        {this.star()}
        {this.star()}
        {this.star()}
        {this.star1()}
        {this.star1()}
      </React.Fragment>
    );
  }
  fourstar() {
    return (
      <React.Fragment>
        {this.star()}
        {this.star()}
        {this.star()}
        {this.star()}
        {this.star1()}
      </React.Fragment>
    );
  }
  fivestar() {
    return (
      <React.Fragment>
        {this.star()}
        {this.star()}
        {this.star()}
        {this.star()}
        {this.star()}
      </React.Fragment>
    );
  }
  getTheStars(level) {
    switch (level) {
      case 1:
        return this.onestar();
        break;
      case 2:
        return this.twostar();
        break;
      case 3:
        return this.threestar();
        break;
      case 4:
        return this.fourstar();
        break;
      case 5:
        return this.fivestar();
        break;
      default:
        this.fourstar();
    }
  }

  render() {
    const skillComponents = this.state.skills.map((skilll) => {
      return (
        <SkillComponent
          skillname={skilll.skill}
          stars={this.getTheStars(skilll.level)}
        />
      );
    });
    return (
      <React.Fragment>
        <div>
          <h1>Skills</h1>
        </div>
        <div>{skillComponents}</div>
      </React.Fragment>
    );
  }
}

export default Skills;
