import React from "react";

function ProjectComponent(props) {
  return (
    <div className="flex-items" id={props.project.project_name}>
      <div>
        <img
          src={props.project.imageurl}
          alt="projectimage"
          className="project-image"
        />
      </div>
      <div>
        <h3 className="project-name">{props.project.project_name}</h3>
        <p className="project-description">{props.project.description}</p>
        <div>
          <a
            href={props.project.behance_url}
            target="_blank"
            style={{ display: props.project.behance_url ? "inline" : "none" }}
          >
            Behance
          </a>
          <a
            href={props.project.github_url}
            style={{ display: props.project.github_url ? "inline" : "none" }}
            target="_blank"
          >
            Github
          </a>
        </div>
      </div>
    </div>
  );
}

export default ProjectComponent;
