import React, { Component } from "react";

class Contact extends Component {
  state = {};
  render() {
    return (
      <div className="content-wrap">
        <h1>Contact</h1>

        <div className="flex-container contact-info-div">
          <div className="flex-items">
            <table>
              <tr>
                <td>
                  <img
                    src="Icons/email96.png"
                    alt="email-icon"
                    className="email-icon"
                  ></img>
                </td>
                <td>
                  <a
                    href="mailto:pattapagalubhupathi@outlook.com"
                    className="emailid"
                  >
                    pattapagalubhupathi@outlook.com
                  </a>
                </td>
              </tr>
            </table>
          </div>
          <div className="flex-items">
            <table>
              <tr>
                <td>
                  <img
                    src="Icons/phone96.png"
                    alt="mobile-icon"
                    className="mobile-icon"
                  ></img>
                </td>
                <td>
                  <a href="tel:+919494692189" className="mobileno">
                    +91-9494692189
                  </a>
                </td>
              </tr>
            </table>
          </div>
        </div>

        <div className="flex-container social-media-div">
          <div className="flex-items">
            <button
              target="_blank"
              onClick={() => (
                (window.location.href =
                  "https://www.linkedin.com/in/pattapagalu-bhupathi/"),
                "_blank"
              )}
            >
              <img
                src="/Icons/linkedin.svg"
                alt="Linkedin Icon"
                author="freepik in flaticon"
              ></img>
            </button>
          </div>
          <div className="flex-items">
            <button
              target="_blank"
              onClick={() => (
                (window.location.href = "https://twitter.com/pattapagalubhu1"),
                "_blank"
              )}
            >
              <img
                src="/Icons/twitter.svg"
                alt="Twitter Icon"
                author="freepik in flaticon"
              ></img>
            </button>
          </div>
          <div className="flex-items">
            <button
              target="_blank"
              onClick={() => (
                (window.location.href =
                  "https://www.behance.net/bhupathipattapagalu"),
                "_blank"
              )}
            >
              <img
                src="/Icons/behance.svg"
                alt="Behance Icon"
                author="freepik in flaticon"
              ></img>
            </button>
          </div>
          <div className="flex-items">
            <button
              target="_blank"
              onClick={() => (
                (window.location.href = "https://github.com/bhupathi189/"),
                "_blank"
              )}
            >
              <img
                src="/Icons/github.svg"
                alt="Github Icon"
                author="pixel-perfect in flaticon"
              ></img>
            </button>
          </div>
          <div className="flex-items">
            <button
              target="_blank"
              onClick={() => (
                (window.location.href = "https://dribbble.com/bhupathi189"),
                "_blank"
              )}
            >
              <img
                src="/Icons/dribbble(color).svg"
                alt="Dribble Icon"
                className="dribbble-icon"
                author="freepik in flaticon"
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
